// lambda/graphql/index.ts
import { randomUUID } from "crypto";
import { DynamoDBClient } from "@aws-sdk/client-dynamodb";
import {
  DynamoDBDocumentClient,
  GetCommand,
  PutCommand,
  UpdateCommand,
  DeleteCommand,
  QueryCommand,
} from "@aws-sdk/lib-dynamodb";
import type { AppSyncResolverEvent, AppSyncIdentityCognito } from "aws-lambda";
import { SFNClient, StartExecutionCommand } from "@aws-sdk/client-sfn";

const TABLE =
  process.env.TABLE_NAME ||
  process.env.CLOSET_TABLE ||
  (() => {
    throw new Error("Missing env TABLE_NAME/CLOSET_TABLE");
  })();

const APPROVAL_SM_ARN = process.env.APPROVAL_SM_ARN || "";
const STATUS_GSI = process.env.STATUS_GSI || "gsi2";

const ddb = DynamoDBDocumentClient.from(new DynamoDBClient({}));
const sfn = new SFNClient({});

// ──────────────────────────────────────────────────────────────────────────────
// Types & helpers
// ──────────────────────────────────────────────────────────────────────────────

type ClosetStatus = "DRAFT" | "PENDING" | "APPROVED" | "REJECTED" | "PUBLISHED";

interface ClosetItem {
  id: string;
  userId: string;
  ownerSub: string;
  title?: string | null;
  status: ClosetStatus;
  mediaKey?: string | null;
  createdAt: string;
  updatedAt: string;
  audience?: "PUBLIC" | "BESTIE" | "EXCLUSIVE" | null;

  // single-table attributes (optional when coming from DynamoDB)
  pk?: string;
  sk?: string;
  gsi1pk?: string;
  gsi1sk?: string;
  gsi2pk?: string;
  gsi2sk?: string;
  approvalToken?: string;
  reason?: string | null;
}

const nowIso = () => new Date().toISOString();

const assertAuth = (sub?: string) => {
  if (!sub) throw new Error("Unauthenticated");
};

const isAdmin = (ident?: AppSyncIdentityCognito) => {
  const claims = (ident as any)?.claims || {};
  const groups = claims["cognito:groups"];
  return Array.isArray(groups)
    ? groups.includes("ADMIN")
    : `${groups || ""}`.includes("ADMIN");
};

const keysForId = (id: string) => ({ pk: `ITEM#${id}`, sk: "META" });

function decodeToken(t?: string | null) {
  if (!t) return undefined;
  try {
    return JSON.parse(Buffer.from(String(t), "base64").toString("utf8"));
  } catch {
    return undefined;
  }
}

function encodeToken(key: any) {
  if (!key) return null;
  return Buffer.from(JSON.stringify(key), "utf8").toString("base64");
}

/**
 * Ensure we always have a non-empty userId and ownerSub on items.
 * - userId comes from item.userId, item.ownerSub, or OWNER#... gsi1pk
 * - ownerSub falls back to userId
 */
function materializeUserId(item: any) {
  if (!item) return item;

  const fromGsi1 =
    typeof item?.gsi1pk === "string" && item.gsi1pk.startsWith("OWNER#")
      ? item.gsi1pk.slice("OWNER#".length)
      : undefined;

  const userId = item.userId ?? item.ownerSub ?? fromGsi1 ?? "";
  const ownerSub = item.ownerSub ?? userId;

  return {
    ...item,
    userId,
    ownerSub,
  };
}

const toPublic = (raw: any): ClosetItem => {
  const m = materializeUserId(raw);
  return {
    id: m.id,
    userId: m.userId,
    ownerSub: m.ownerSub,
    title: m.title ?? null,
    status: m.status,
    mediaKey: m.mediaKey ?? null,
    createdAt: m.createdAt,
    updatedAt: m.updatedAt,
    audience: m.audience ?? null,
  };
};

// Episodes gate: public ids allowed without checks
const PUBLIC_EPISODES = new Set<string>(["ep-001"]);

// ──────────────────────────────────────────────────────────────────────────────
// Main AppSync handler
// ──────────────────────────────────────────────────────────────────────────────

export const handler = async (event: AppSyncResolverEvent<any>) => {
  const field = event.info.fieldName;
  const ident = event.identity as AppSyncIdentityCognito | undefined;
  const sub = ident?.sub;

  switch (field) {
    // ------------------ Query.isEpisodeEarlyAccess ------------------
    case "isEpisodeEarlyAccess": {
      const epId = String(event.arguments?.id || "").trim();

      // Public episode? always allowed.
      if (PUBLIC_EPISODES.has(epId)) {
        return { allowed: true, reason: null };
      }

      // Must be signed in to check early drops.
      if (!sub) {
        return { allowed: false, reason: "Sign in to check early access." };
      }

      // Look at profile flags that indicate Bestie.
      const got = await ddb.send(
        new GetCommand({
          TableName: TABLE,
          Key: { pk: `USER#${sub}`, sk: "PROFILE" },
        })
      );
      const p = got.Item || {};
      const bestieActive =
        p.bestie?.BOOL === true ||
        p.bestieActive?.BOOL === true ||
        p.tier?.S === "BESTIE" ||
        p.tier === "BESTIE";

      if (bestieActive) return { allowed: true, reason: null };

      // Optional: dedicated BESTIE row
      try {
        const bestie = await ddb.send(
          new GetCommand({
            TableName: TABLE,
            Key: { pk: `USER#${sub}`, sk: "BESTIE" },
          })
        );
        if (bestie.Item?.active === true || bestie.Item?.active?.BOOL === true) {
          return { allowed: true, reason: null };
        }
      } catch {
        /* ignore */
      }

      return {
        allowed: false,
        reason: "This is an early drop for Bestie members.",
      };
    }

    // ------------------ Query.myCloset ------------------
    case "myCloset": {
      assertAuth(sub);

      // Most-recent first by created/updated time
      const out = await ddb.send(
        new QueryCommand({
          TableName: TABLE,
          IndexName: "gsi1",
          KeyConditionExpression: "gsi1pk = :p",
          ExpressionAttributeValues: { ":p": `OWNER#${sub}` },
          ScanIndexForward: false,
          Limit: 50,
        })
      );
      return (out.Items || [])
        .filter((x) => x.sk === "META")
        .map(toPublic);
    }

    // ------------------ Query.closetFeed ------------------
    case "closetFeed": {
      const { limit, nextToken } = event.arguments || {};
      const pageLimit = Math.min(Math.max(Number(limit) || 12, 1), 50); // 1–50
      const startKey = decodeToken(nextToken);

      const out = await ddb.send(
        new QueryCommand({
          TableName: TABLE,
          IndexName: STATUS_GSI, // usually "gsi2"
          KeyConditionExpression: "#gpk = :pk",
          ExpressionAttributeNames: { "#gpk": "gsi2pk" },
          ExpressionAttributeValues: { ":pk": "STATUS#APPROVED" },
          ScanIndexForward: false, // newest first
          Limit: pageLimit,
          ExclusiveStartKey: startKey,
        })
      );

      const all = (out.Items || [])
        .filter((x) => x.sk === "META")
        .map(toPublic);

      // Privacy fence: APPROVED/PUBLISHED + PUBLIC audience only
      const visible = all.filter((it) => {
        const statusOk = it.status === "APPROVED" || it.status === "PUBLISHED";
        const audience = (it.audience as any) || "PUBLIC";
        const audienceOk = audience === "PUBLIC";
        return statusOk && audienceOk;
      });

      return {
        items: visible,
        nextToken: encodeToken(out.LastEvaluatedKey),
      };
    }

    // ------------------ Mutation.createClosetItem ------------------
    case "createClosetItem": {
      assertAuth(sub);
      const { title, mediaKey } = (event.arguments || {}) as {
        title?: string | null;
        mediaKey?: string | null;
      };

      const id = randomUUID();
      const ts = nowIso();
      const item: ClosetItem = {
        id,
        userId: sub!,
        ownerSub: sub!,
        title: (title ?? "").trim() || null,
        mediaKey: (mediaKey ?? "").trim() || null,
        status: "DRAFT",
        createdAt: ts,
        updatedAt: ts,
        audience: "PUBLIC",
      };

      await ddb.send(
        new PutCommand({
          TableName: TABLE,
          Item: {
            ...item,
            ...keysForId(id),
            gsi1pk: `OWNER#${sub}`,
            gsi1sk: ts,
            gsi2pk: "STATUS#DRAFT",
            gsi2sk: ts,
          },
          ConditionExpression: "attribute_not_exists(pk)",
        })
      );

      return item;
    }

    // ------------------ Mutation.updateClosetMediaKey ------------------
    case "updateClosetMediaKey": {
      assertAuth(sub);
      const { id, mediaKey } = event.arguments as {
        id: string;
        mediaKey: string;
      };

      const got = await ddb.send(
        new GetCommand({ TableName: TABLE, Key: keysForId(id) })
      );
      if (!got.Item) throw new Error("Not found");
      if (got.Item.ownerSub !== sub && !isAdmin(ident)) {
        throw new Error("Forbidden");
      }

      const ts = nowIso();
      const updated = await ddb.send(
        new UpdateCommand({
          TableName: TABLE,
          Key: keysForId(id),
          UpdateExpression: "SET mediaKey = :k, updatedAt = :u",
          ExpressionAttributeValues: { ":k": mediaKey, ":u": ts },
          ReturnValues: "ALL_NEW",
        })
      );
      return toPublic(updated.Attributes);
    }

    // ------------------ Mutation.requestClosetApproval ------------------
    case "requestClosetApproval": {
      assertAuth(sub);
      const { id } = event.arguments as { id: string };

      const got = await ddb.send(
        new GetCommand({ TableName: TABLE, Key: keysForId(id) })
      );
      if (!got.Item) throw new Error("Not found");
      if (got.Item.ownerSub !== sub && !isAdmin(ident)) {
        throw new Error("Forbidden");
      }

      const ts = nowIso();
      await ddb.send(
        new UpdateCommand({
          TableName: TABLE,
          Key: keysForId(id),
          UpdateExpression:
            "SET #s = :p, gsi2pk = :g, gsi2sk = :ts, updatedAt = :ts",
          ExpressionAttributeNames: { "#s": "status" },
          ExpressionAttributeValues: {
            ":p": "PENDING",
            ":g": "STATUS#PENDING",
            ":ts": ts,
          },
        })
      );

      if (APPROVAL_SM_ARN) {
        await sfn.send(
          new StartExecutionCommand({
            stateMachineArn: APPROVAL_SM_ARN,
            input: JSON.stringify({ itemId: id, ownerSub: sub }),
          })
        );
      }
      return true;
    }

    // ------------------ Query.adminListPending ------------------
    case "adminListPending": {
      if (!isAdmin(ident)) throw new Error("Not authorized");

      const out = await ddb.send(
        new QueryCommand({
          TableName: TABLE,
          IndexName: STATUS_GSI,
          KeyConditionExpression: "gsi2pk = :p",
          ExpressionAttributeValues: { ":p": "STATUS#PENDING" },
          ScanIndexForward: false,
          Limit: 100,
        })
      );

      return (out.Items || []).map(toPublic);
    }

    // ------------------ Mutation.adminApproveItem ------------------
    case "adminApproveItem": {
      if (!isAdmin(ident)) throw new Error("Not authorized");
      const { id } = event.arguments as { id: string };

      const ts = nowIso();
      await ddb.send(
        new UpdateCommand({
          TableName: TABLE,
          Key: keysForId(id),
          UpdateExpression:
            "SET #s = :a, gsi2pk = :g, gsi2sk = :ts, updatedAt = :ts REMOVE approvalToken",
          ExpressionAttributeNames: { "#s": "status" },
          ExpressionAttributeValues: {
            ":a": "APPROVED",
            ":g": "STATUS#APPROVED",
            ":ts": ts,
          },
        })
      );
      return true;
    }

    // ------------------ Mutation.adminRejectItem ------------------
    case "adminRejectItem": {
      if (!isAdmin(ident)) throw new Error("Not authorized");
      const { id, reason } = event.arguments as {
        id: string;
        reason?: string;
      };

      const ts = nowIso();
      await ddb.send(
        new UpdateCommand({
          TableName: TABLE,
          Key: keysForId(id),
          UpdateExpression:
            "SET #s = :r, gsi2pk = :g, gsi2sk = :ts, updatedAt = :ts, reason = :why REMOVE approvalToken",
          ExpressionAttributeNames: { "#s": "status" },
          ExpressionAttributeValues: {
            ":r": "REJECTED",
            ":g": "STATUS#REJECTED",
            ":ts": ts,
            ":why": reason ?? "Rejected by admin",
          },
        })
      );
      return true;
    }

    // ------------------ Mutation.adminSetClosetAudience ------------------
    case "adminSetClosetAudience": {
      if (!isAdmin(ident)) throw new Error("Not authorized");
      const { id, audience } = event.arguments as {
        id: string;
        audience: "PUBLIC" | "BESTIE" | "EXCLUSIVE";
      };

      const ts = nowIso();
      const updated = await ddb.send(
        new UpdateCommand({
          TableName: TABLE,
          Key: keysForId(id),
          UpdateExpression: "SET audience = :aud, updatedAt = :ts",
          ExpressionAttributeValues: {
            ":aud": audience,
            ":ts": ts,
          },
          ReturnValues: "ALL_NEW",
        })
      );

      return toPublic(updated.Attributes);
    }

    // ------------------ Mutation.deleteClosetItem (optional) ------------------
    case "deleteClosetItem": {
      assertAuth(sub);
      const { id } = event.arguments as { id: string };

      const got = await ddb.send(
        new GetCommand({ TableName: TABLE, Key: keysForId(id) })
      );
      if (!got.Item) throw new Error("Not found");
      if (got.Item.ownerSub !== sub && !isAdmin(ident)) {
        throw new Error("Forbidden");
      }

      await ddb.send(
        new DeleteCommand({ TableName: TABLE, Key: keysForId(id) })
      );
      return true;
    }

    // ----------------------------------------------------------------
    default:
      throw new Error(`Unhandled field: ${field}`);
  }
};
